/* eslint-disable react/no-array-index-key */
/* eslint-disable react/prop-types */
// TODO проверь все файлы на prop-types
import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Checkbox from '@material-ui/core/Checkbox';

import theme from '../../themes/themeLight';
import { SETTINGS } from '../../constantas/settings';
import { selectItem, stickyColumn } from '../../actions';

const tableStyles = themeHere => ({
  head: {
    display: 'flex',
    boxShadow: themeHere.shadows[1],
    '& div': {
      padding: themeHere.spacing(1),
      textAlign: 'center',
      lineHeight: `${SETTINGS.itemHeight}px`,
      borderRight: `1px solid ${themeHere.palette.secondary.light}`,
      borderTop: `1px solid ${themeHere.palette.secondary.light}`,
      '&:last-child': {
        borderRight: 'none',
      },
    },
  },
  checkbox: {
    minWidth: 50,
    padding: themeHere.spacing(1),
    borderBottom: `1px solid ${themeHere.palette.secondary.light}`,
    borderRight: `1px solid ${themeHere.palette.secondary.light}`,
  },
  checkboxChecked: {
    color: themeHere.palette.primary.main,
  },
  center: {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    padding: themeHere.spacing(1),
    borderBottom: `1px solid ${themeHere.palette.secondary.light}`,
    borderRight: `1px solid ${themeHere.palette.secondary.light}`,
  },
  textVerCenter: {
    display: 'flex',
    alignItems: 'center',
    padding: themeHere.spacing(1),
    borderBottom: `1px solid ${themeHere.palette.secondary.light}`,
    borderRight: `1px solid ${themeHere.palette.secondary.light}`,
  },
  cell: {
    minWidth: 200,
    backgroundColor: themeHere.palette.background.default,
    '&:lastChild': {
      borderRight: 'none',
    },
  },
  index: {
    minWidth: 45,
  },
  icon: {
    minWidth: 50,
  },
  img: {
    borderRadius: '50%',
  },
  foundedYear: {
    minWidth: 120,
    justifyContent: 'flex-end',
  },
  status: {
    minWidth: 100,
  },
  investSum: {
    minWidth: 170,
    justifyContent: 'flex-end',
  },
  row: {
    display: 'flex',
    height: SETTINGS.itemHeight,
  },
  link: {
    color: themeHere.palette.info.main,
  },
  highlightedText: {
    backgroundColor: themeHere.palette.secondary.light,
    borderRadius: 4,
    padding: '0 5px',
  },
  cellSelected: {
    backgroundColor: '#FFC9CC',
  },
  stickyColumn: {
    position: 'sticky',
    left: 0,
  },
});

const coordinateFormatting = n =>
  n.toLocaleString('ru-RU', { maximumFractionDigits: 2, minimumFractionDigits: 2 });

const ShowCoors = ({ coors, styles }) => {
  const lat = coordinateFormatting(coors.latitude);
  const long = coordinateFormatting(coors.longitude);
  return (
    <>
      <span>[</span>
      <span style={styles}>{lat}</span>
      <span>;&#8194;</span>
      <span style={styles}>{long}</span>
      <span>]</span>
    </>
  );
};

ShowCoors.defaultProps = {
  styles: {},
  coors: { latitude: 0, longitude: 0 },
};

ShowCoors.propTypes = {
  styles: PropTypes.shape({
    backgroundColor: PropTypes.string,
    borderRadius: PropTypes.number,
  }),
  coors: PropTypes.shape({ latitude: PropTypes.number, longitude: PropTypes.number }),
};

const ConnectedRowTemplate = props => {
  const { item, selected, selectItem, columnSticky } = props;
  const styles = tableStyles(theme);
  const {
    index,
    id,
    icon,
    company,
    url,
    foundedYear,
    founder,
    investSum,
    status,
    geo,
    categoryList,
  } = item;

  const isSelected = name => selected.indexOf(name) !== -1;
  const [isItemSelected, setIsItemSelected] = useState(false);
  useEffect(() => {
    setIsItemSelected(isSelected(id));
  }, [selected]);

  const labelId = `enhanced-table-checkbox-${index}`;

  const handleSelect = () => {
    const selectedItems = isItemSelected ? selected.filter(el => el !== id) : [...selected, id];

    setIsItemSelected(!isItemSelected);
    selectItem({ selected: selectedItems });
  };
  const styckyColumnStyle = () => {
    if (isItemSelected && columnSticky) {
      return {
        ...styles.cell,
        ...styles.textVerCenter,
        ...styles.cellSelected,
        ...styles.stickyColumn,
      };
    }
    if (isItemSelected && !columnSticky) {
      return { ...styles.cell, ...styles.textVerCenter, ...styles.cellSelected };
    }
    if (!isItemSelected && columnSticky) {
      return { ...styles.cell, ...styles.textVerCenter, ...styles.stickyColumn };
    }
    return { ...styles.cell, ...styles.textVerCenter };
  };
  // TODO возможно вынести стили
  return (
    <div style={styles.row} key={id}>
      <div
        style={isItemSelected ? { ...styles.checkbox, ...styles.cellSelected } : styles.checkbox}
      >
        <Checkbox
          checked={isItemSelected}
          inputProps={{ 'aria-labelledby': labelId }}
          onChange={handleSelect}
          color="secondary"
          style={isItemSelected ? styles.checkboxChecked : null}
        />
      </div>
      <div
        style={
          isItemSelected
            ? { ...styles.index, ...styles.center, ...styles.cellSelected }
            : { ...styles.index, ...styles.center }
        }
      >
        {index + 1}
      </div>
      <div
        style={
          isItemSelected
            ? { ...styles.icon, ...styles.center, ...styles.cellSelected }
            : { ...styles.icon, ...styles.center }
        }
      >
        <img src={icon} alt={company} style={styles.img} />
      </div>
      <div style={styckyColumnStyle()}>
        <a href={url} target="_blank" rel="noopener noreferrer" style={styles.link}>
          {company}
        </a>
      </div>
      <div
        style={
          isItemSelected
            ? { ...styles.foundedYear, ...styles.textVerCenter, ...styles.cellSelected }
            : { ...styles.foundedYear, ...styles.textVerCenter }
        }
      >
        {foundedYear}
      </div>
      <div
        style={
          isItemSelected
            ? { ...styles.cell, ...styles.textVerCenter, ...styles.cellSelected }
            : { ...styles.cell, ...styles.textVerCenter }
        }
      >
        {founder}
      </div>
      <div
        style={
          isItemSelected
            ? { ...styles.investSum, ...styles.textVerCenter, ...styles.cellSelected }
            : { ...styles.investSum, ...styles.textVerCenter }
        }
      >
        {investSum.toLocaleString()}
      </div>
      <div
        style={
          isItemSelected
            ? { ...styles.cell, ...styles.textVerCenter, ...styles.cellSelected }
            : { ...styles.cell, ...styles.textVerCenter }
        }
      >
        {categoryList.map((category, i) => (
          <span key={`${id}-${category}-${i}`}>
            |&nbsp;
            <span style={styles.highlightedText}>{category === '' ? null : category}</span>
            {i === categoryList.length - 1 ? ' |' : null}
          </span>
        ))}
      </div>
      <div
        style={
          isItemSelected
            ? { ...styles.status, ...styles.textVerCenter, ...styles.cellSelected }
            : { ...styles.status, ...styles.textVerCenter }
        }
      >
        {status}
      </div>
      <div
        style={
          isItemSelected
            ? { ...styles.cell, ...styles.textVerCenter, ...styles.cellSelected }
            : { ...styles.cell, ...styles.textVerCenter }
        }
      >
        <ShowCoors coors={geo} styles={styles.highlightedText} />
      </div>
    </div>
  );
};

const mapStateToProps = state => {
  return {
    selected: state.selected,
    columnSticky: state.columnSticky,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    selectItem: data => dispatch(selectItem(data)),
    stickyColumn: data => dispatch(stickyColumn(data)),
  };
};
const RowTemplate = connect(mapStateToProps, mapDispatchToProps)(ConnectedRowTemplate);
export default RowTemplate;
