import Fakerator from 'fakerator';
import drawIcon from './icon-generator';

const dataGenerator = () => {
  const fakerator = Fakerator('ru-RU');

  fakerator.seed(Math.floor(Math.random() * 10000000));

  const id = fakerator.random.masked('aaa-AAA_999999:*');
  const icon = drawIcon();
  const company = fakerator.company.name();
  const url = `https://${fakerator.random.string(10)}.com`;
  const foundedYear = fakerator.date.past(10, new Date()).getFullYear();
  const founder = fakerator.names.name();
  const investSum = fakerator.random.number(5, 2000) * 1000;
  const stillAlive = fakerator.random.boolean();
  const status = stillAlive ? 'operating' : 'closed';
  const geo = fakerator.address.geoLocation();
  const categoryList = [];

  for (let j = 0; j < 2; j += 1) {
    // TODO сделать проверку есть такое значение уже в массиве
    const k = fakerator.random.number(0, 5);
    const value = () => {
      switch (k) {
        case 5:
          return 'Games';
        case 4:
          return 'Tourism';
        case 3:
          return 'IT';
        case 2:
          return 'Media';
        case 1:
          return 'Medicine';
        default:
          return undefined;
      }
    };
    if (value() !== undefined) {
      categoryList.push(value());
    }
  }
  return {
    id,
    icon,
    company,
    url,
    foundedYear,
    founder,
    investSum,
    status,
    geo,
    categoryList,
  };
};

export default dataGenerator;
