import {
  REFRESH_DATA,
  DELETE_ITEM,
  SELECT_ITEM,
  IS_STICKY_COLUMN,
} from './constantas/action-types';

const refreshData = payload => {
  return { type: REFRESH_DATA, payload };
};

const deleteItem = payload => {
  return { type: DELETE_ITEM, payload };
};

const selectItem = payload => {
  return { type: SELECT_ITEM, payload };
};

const stickyColumn = payload => {
  return { type: IS_STICKY_COLUMN, payload };
};

export { refreshData, deleteItem, selectItem, stickyColumn };
