import { DELETE_ITEM } from './constantas/action-types';
// Redux can hold business logic inside its own layer (middleware)
//  Looks like we want to check the action payload (and the title property) before the action is passed to the reducer. There should be a way for tapping into the application's flow. And guess what, that's exactly what a Redux middleware does.

const middleware = ({ dispatch }) => {
  return next => {
    return action => {
      if (action.type === DELETE_ITEM) {
        return dispatch({ type: 'DELETE_ITEM' });
      }
      return next(action);
    };
  };
};

export default middleware;
