import { REFRESH_DATA, SELECT_ITEM, IS_STICKY_COLUMN } from './constantas/action-types';
import data from './utils/data';

const initialState = {
  initialData: data,
  shownData: [],
  selected: [],
  isColumnSticky: false,
};

const reducer = (state = initialState, action) => {
  switch (action.type) {
    case REFRESH_DATA:
      return { ...state, ...action.payload };
    case SELECT_ITEM:
      return { ...state, ...action.payload };
    case IS_STICKY_COLUMN:
      return { ...state, ...action.payload };
    default:
      return state;
  }
};

export default reducer;
