/* eslint-disable react/prop-types */
import React, { useState, createRef, useEffect } from 'react';
// import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';

import theme from '../../themes/themeLight';
import { refreshData, selectItem, stickyColumn } from '../../actions';
import getData from '../../utils/get-data';
import { SETTINGS } from '../../constantas/settings';
import { getComparator, stableSort } from '../../utils/sort-functions';
import TableHeader from './table-head';
import RowTemplate from './row-template';

const setInitialState = settings => {
  const { itemHeight, amount, tolerance, minIndex, maxIndex, startIndex } = settings;
  const viewportHeight = amount * itemHeight;
  const totalHeight = (maxIndex - minIndex + 1) * itemHeight;
  const toleranceHeight = tolerance * itemHeight;
  const bufferHeight = viewportHeight + 2 * toleranceHeight;
  const bufferedItems = amount + 2 * tolerance;
  const itemsAbove = startIndex - tolerance - minIndex;
  const topPaddingHeight = itemsAbove * itemHeight;
  const bottomPaddingHeight = totalHeight - topPaddingHeight;
  const initialPosition = topPaddingHeight + toleranceHeight;
  return {
    settings,
    viewportHeight,
    totalHeight,
    toleranceHeight,
    bufferHeight,
    bufferedItems,
    topPaddingHeight,
    bottomPaddingHeight,
    initialPosition,
  };
};

const useStyles = makeStyles({
  virtScroller: {
    height: 'calc(100vh - 140px)',
    overflow: 'auto',
    minHeight: 500,
  },
});

const ConnectedVirtualScroller = props => {
  // console.log('props: ', props);
  const styles = useStyles();
  // TODO удалить здесь все, что относится к columnSticky
  const { selected, selectItem, shownData, stickyColumn, initialData } = props;
  const [state, setState] = useState(setInitialState(SETTINGS));
  const [order, setOrder] = useState('asc');
  const [orderBy, setOrderBy] = useState('index');
  const [scrollVert, setScrollVert] = React.useState(0);

  const viewportElement = createRef();
  const { itemHeight, minIndex, tolerance } = SETTINGS;
  const toleranceHeight = tolerance * itemHeight;

  const runScroller = ({ target }) => {
    const totalHeight = viewportElement.current.clientHeight;
    const amount = Math.ceil(totalHeight / itemHeight);
    const bufferedItems = amount + tolerance;

    const { scrollTop } = target;
    const { scrollLeft } = target;

    if (scrollVert !== viewportElement.current.scrollTop) {
      const index = minIndex + Math.floor((scrollTop - toleranceHeight) / itemHeight);
      const data = getData(index, bufferedItems, minIndex, initialData);
      const topPaddingHeight = Math.max((index - minIndex) * itemHeight, 0);
      const bottomPaddingHeight = Math.max(
        totalHeight - topPaddingHeight - data.length * itemHeight,
        0
      );
      props.refreshData({ shownData: data });
      setState({ ...state, topPaddingHeight, bottomPaddingHeight });
    }

    stickyColumn({ isColumnSticky: scrollLeft >= 150 });
    setScrollVert(viewportElement.current.scrollTop);
  };

  useEffect(() => {
    const data = getData(0, 25, 0, initialData);
    props.refreshData({ shownData: data });
    if (!state.initialPosition) {
      runScroller({ target: { scrollTop: 0 } });
    }
  }, []);

  const handleSelectAllClick = event => {
    // TODO сделать так, чтоб выбирались все 1000
    if (event.target.checked) {
      const allSelected = initialData.map(n => n.id);
      selectItem({ selected: allSelected });
    } else {
      selectItem({ selected: [] });
    }
  };

  const handleRequestSort = (e, property) => {
    const isAsc = orderBy === property && order === 'asc';
    setOrder(isAsc ? 'desc' : 'asc');
    setOrderBy(property);
  };

  const { topPaddingHeight, bottomPaddingHeight } = state;
  return (
    <div className={styles.virtScroller} ref={viewportElement} onScroll={runScroller}>
      <TableHeader
        numSelected={selected.length}
        order={order}
        orderBy={orderBy}
        onSelectAllClick={handleSelectAllClick}
        onRequestSort={handleRequestSort}
        rowCount={shownData.length}
      />
      <div style={{ height: topPaddingHeight }} />
      {stableSort(shownData, getComparator(order, orderBy)).map(item => (
        <RowTemplate item={item} key={item.id} />
      ))}
      <div style={{ height: bottomPaddingHeight }} />
    </div>
  );
};

const mapStateToProps = ({ initialData, shownData, selected }) => ({
  initialData,
  shownData,
  selected,
});

const mapDispatchToProps = dispatch => {
  return {
    refreshData: rows => dispatch(refreshData(rows)),
    selectItem: selected => dispatch(selectItem(selected)),
    stickyColumn: data => dispatch(stickyColumn(data)),
  };
};

const VirtualScroller = connect(mapStateToProps, mapDispatchToProps)(ConnectedVirtualScroller);

export default VirtualScroller;
