import dataGenerator from './data-generator';
import { ROW_NUMBER } from '../constantas/settings';

const data = (n, generator) => {
  const arr = [];
  for (let i = 0; i < n; i += 1) {
    arr.push({ ...generator(), ...{ index: i } });
  }
  return arr;
};

export default data(ROW_NUMBER, dataGenerator);
