import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import CsvDownloader from 'react-csv-downloader';
import clsx from 'clsx';
import { lighten, makeStyles } from '@material-ui/core/styles';
import { Toolbar, Typography, IconButton, Tooltip } from '@material-ui/core';
import GetAppIcon from '@material-ui/icons/GetApp';

// import DeleteIcon from '@material-ui/icons/Delete';
// import FilterListIcon from '@material-ui/icons/FilterList';

import theme from '../../themes/themeLight';
import { selectItem } from '../../actions';
import dataToCSVPreparation from '../../utils/data-to-CSV-preparation';

const useToolbarStyles = makeStyles(themeHere => ({
  root: {
    display: 'flex',
    justifyContent: 'space-between',
    height: 100,
    paddingBottom: themeHere.spacing(10),
    paddingLeft: themeHere.spacing(2),
    paddingRight: themeHere.spacing(1),
  },
  highlight:
    themeHere.palette.type === 'light'
      ? {
          color: themeHere.palette.secondary.main,
          backgroundColor: lighten(themeHere.palette.secondary.light, 0.85),
        }
      : {
          color: themeHere.palette.text.primary,
          backgroundColor: themeHere.palette.secondary.dark,
        },
  description: {
    color: themeHere.palette.primary.main,
    maxWidth: 400,
    '& *': {
      display: 'block',
    },
  },
}));

const ConnectedTableToolbar = props => {
  const classes = useToolbarStyles(theme);
  const { selected, initialData } = props;
  const numSelected = selected.length;
  const [csvData, setCsvData] = useState(dataToCSVPreparation(initialData));

  useEffect(() => {
    setCsvData(dataToCSVPreparation(initialData));
  }, [initialData]);

  return (
    <Toolbar
      className={clsx(classes.root, {
        [classes.highlight]: numSelected > 0,
      })}
    >
      {numSelected > 0 ? (
        <Typography color="primary" variant="subtitle1" className={classes.description}>
          {numSelected} selected
        </Typography>
      ) : (
        <div className={classes.description}>
          <Typography variant="h6" id="tableTitle">
            StartUp Investments
          </Typography>
          <Typography variant="body2">
            Information about startup companies and investment
          </Typography>
        </div>
      )}
      {numSelected > 0 ? (
        <Tooltip title="Delete">
          <IconButton aria-label="delete">
            {/* <DeleteIcon /> // TODO сделать так, чтоб удаллись выбранные элменты */}
          </IconButton>
        </Tooltip>
      ) : (
        // TODO узнать как можно использовать эту фильтрацию
        <Tooltip title="download CSV">
          <IconButton aria-label="filter list">
            <CsvDownloader datas={csvData} filename="csvData.csv" separator=";">
              <GetAppIcon />
            </CsvDownloader>
          </IconButton>
        </Tooltip>
      )}
    </Toolbar>
  );
};
ConnectedTableToolbar.defaultProps = {
  selected: [],
  initialData: [],
};

ConnectedTableToolbar.propTypes = {
  selected: PropTypes.arrayOf(PropTypes.string),
  initialData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      index: PropTypes.number,
      icon: PropTypes.string,
      company: PropTypes.string,
      url: PropTypes.string,
      foundedYear: PropTypes.number,
      founder: PropTypes.string,
      investSum: PropTypes.number,
      status: PropTypes.string,
      geo: PropTypes.shape({
        latitude: PropTypes.number,
        longitude: PropTypes.number,
      }),
      categoryList: PropTypes.arrayOf(PropTypes.string),
    })
  ),
};

const mapStateToProps = ({ initialData, selected }) => ({ initialData, selected });

const mapDispatchToProps = dispatch => {
  return {
    selectItem: data => dispatch(selectItem(data)),
  };
};

const TableToolbar = connect(mapStateToProps, mapDispatchToProps)(ConnectedTableToolbar);

export default TableToolbar;
