/* eslint-disable no-nested-ternary */
import React from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import { makeStyles } from '@material-ui/core/styles';
import { TableSortLabel, Checkbox } from '@material-ui/core';

import theme from '../../themes/themeLight';

const headCells = [
  { id: 'index', label: '№' },
  { id: 'icon', label: 'icon' },
  { id: 'company', label: 'Company' },
  { id: 'foundedYear', label: 'Founded in' },
  { id: 'founder', label: 'Founder' },
  { id: 'investSum', label: 'Funding total, USD' },
  { id: 'categoryList', label: 'Business category' },
  { id: 'status', label: 'Status' },
  { id: 'geo', label: 'Geo location' },
];
// TODO баг с sticky column

const useStyles = makeStyles(themeHere => ({
  head: {
    position: 'sticky',
    top: 0,
    display: 'flex',
    zIndex: 2,
    '& div': {
      padding: themeHere.spacing(1),
      textAlign: 'center',
      lineHeight: '50px',
      borderRight: `1px solid ${themeHere.palette.secondary.light}`,
      borderTop: `1px solid ${themeHere.palette.secondary.light}`,
      '&:last-child': {
        borderRight: 'none',
      },
    },
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
  checkbox: {
    minWidth: 50,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
  },
  cell: {
    minWidth: 200,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
    zIndex: 2,
  },
  index: {
    minWidth: 45,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
  },
  icon: {
    minWidth: 50,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
  },
  foundedYear: {
    minWidth: 120,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
  },
  investSum: {
    minWidth: 170,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
  },
  status: {
    minWidth: 100,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
  },
  stickyColumn: {
    position: 'sticky',
    left: 0,
    minWidth: 200,
    backgroundColor: themeHere.palette.background.default,
    boxShadow: themeHere.shadows[1],
    zIndex: 3,
  },
  sortLabel: {
    '& svg': {
      opacity: 0.3,
      '&:hover': {
        opacity: 1,
      },
    },
  },
}));

const ConnectedTableHeader = props => {
  const {
    onSelectAllClick,
    order,
    orderBy,
    numSelected,
    rowCount,
    onRequestSort,
    isColumnSticky,
    initialData,
  } = props;

  const classes = useStyles(theme);

  const createSortHandler = property => event => {
    onRequestSort(event, property);
  };

  return (
    <div className={classes.head}>
      <div className={classes.checkbox}>
        <Checkbox
          indeterminate={numSelected > 0 && numSelected < rowCount}
          checked={rowCount > 0 && numSelected === initialData.length}
          onChange={onSelectAllClick}
          inputProps={{ 'aria-label': 'select all' }}
        />
      </div>
      {headCells.map(headCell => {
        const { id, label } = headCell;
        const divClass =
          id === 'icon'
            ? classes.icon
            : id === 'index'
            ? classes.index
            : id === 'foundedYear'
            ? classes.foundedYear
            : id === 'status'
            ? classes.status
            : id === 'investSum'
            ? classes.investSum
            : id === 'company' && isColumnSticky
            ? classes.stickyColumn
            : classes.cell;
        return (
          <div key={id} className={divClass}>
            {['icon', 'geo'].includes(id) ? (
              label
            ) : (
              <TableSortLabel
                active={orderBy === id}
                direction={orderBy === id ? 'desc' : order}
                onClick={createSortHandler(id)}
                className={classes.sortLabel}
              >
                {label}
                {orderBy === id ? (
                  <span className={classes.visuallyHidden}>
                    {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                  </span>
                ) : null}
              </TableSortLabel>
            )}
          </div>
        );
      })}
    </div>
  );
};

ConnectedTableHeader.defaultProps = {
  numSelected: 0,
  onRequestSort: () => {},
  onSelectAllClick: () => {},
  order: 'asc',
  orderBy: '',
  rowCount: 0,
  isColumnSticky: false,
  initialData: [],
};

ConnectedTableHeader.propTypes = {
  numSelected: PropTypes.number,
  onRequestSort: PropTypes.func,
  onSelectAllClick: PropTypes.func,
  order: PropTypes.oneOf(['asc', 'desc']),
  orderBy: PropTypes.string,
  rowCount: PropTypes.number,
  isColumnSticky: PropTypes.bool,
  initialData: PropTypes.arrayOf(
    PropTypes.shape({
      id: PropTypes.string,
      index: PropTypes.number,
      icon: PropTypes.string,
      company: PropTypes.string,
      url: PropTypes.string,
      foundedYear: PropTypes.number,
      founder: PropTypes.string,
      investSum: PropTypes.number,
      status: PropTypes.string,
      geo: PropTypes.shape({
        latitude: PropTypes.number,
        longitude: PropTypes.number,
      }),
      categoryList: PropTypes.arrayOf(PropTypes.string),
    })
  ),
};

const mapStateToProps = ({ initialData, isColumnSticky }) => ({ initialData, isColumnSticky });

const TableHeader = connect(mapStateToProps, null)(ConnectedTableHeader);

export default TableHeader;
