const dataToCSVPreparation = data => {
  const arr = data.map(
    ({ index, id, company, url, foundedYear, founder, investSum, status, geo, categoryList }) => {
      const categories = categoryList.reduce(
        (accumulator, currentValue) => `${accumulator} ${currentValue},`,
        ''
      );
      const num = n =>
        n.toLocaleString('ru-RU', {
          maximumFractionDigits: 2,
          minimumFractionDigits: 2,
        });
      return {
        index,
        id,
        company,
        url,
        foundedYear,
        founder,
        investSum,
        categories,
        status,
        latitude: num(geo.latitude),
        longitude: num(geo.longitude),
      };
    }
  );
  return arr;
};

export default dataToCSVPreparation;
